<template>
    <v-container>
        <v-card-text align="center"><b><font size="6">i2books</font></b></v-card-text>
        <v-flex xs6 offset-xs3>
        <v-card-text><font size="5"></font></v-card-text>
           <v-text-field v-model="account" label="" class="input-group--focused"></v-text-field>
           <v-text-field v-model="password" :type="'password'" label="" class="input-group--focused"></v-text-field>
           <center><v-btn @click="login" large></v-btn></center>
        </v-flex>
    </v-container>
</template>

<script>

import api from '../../store/api'

export default {
    data(){
        return {
            account:'',
            password:''
        }
    },
    methods:{
        login(){
            api.login(this.account,this.password).then(res=>{
                localStorage.setItem('token',res.data.token)
                this.$router.push('/home/homePage')
            }).catch(error=>{
                alert('');
            })
        }
    }
}
</script>



