<template>
    <v-container>
        <v-layout column>
            <v-btn v-bind:key=item.title v-for="item in list" @click="changeRoute(item.src)">{{item.title}}</v-btn>
        </v-layout>
    </v-container>
</template>

<script>
export default {
    data(){
        return {
            list:[
                {title:'',src:'homePage'},
                {title:'',src:'about'},
                {title:'',src:'publish'},
                {title:'',src:'type'},
                //{title:'',src:'tree'},
                {title:'',src:'product'},
                {title:'',src:'sell'},
                {title:'',src:'member'},
                //{title:'',src:'newsletter'},
                {title:'',src:'order'},
            ]
        }
    },
    methods:{
        changeRoute(src){
            this.$router.push('/home/'+src)
        }
    },
    beforeMount(){

    }
}
</script>

<style>

</style>



