<template>
    <v-container>
        <center>
            <div v-bind:key=item.title v-for="item in list">
                <font size="4" v-if="item.src==path">{{item.title}}</font>
            </div>
        </center>
    </v-container>
</template>

<script>
export default {
    props:['path'],
    data(){
        return{
            list:[
                {title:'',src:'homePage'},
                {title:'',src:'publish'},
                {title:'',src:'product'},
                {title:'',src:'about'},
                {title:'',src:'sell'},
                {title:'',src:'member'},
                {title:'',src:'newsletter'},
                {title:'',src:'type'},
                {title:'',src:'order'},
            ]
        }
    },
    beforeMount(){
    }
}
</script>

<style>

</style>



