<template lang="html">
  <div class="editor">
    <div ref="toolbar" class="toolbar">
    </div>
    <div ref="editor" class="text">
    </div>
  </div>
</template>

<script>
import E from 'wangeditor'
export default {
  name: 'Editorbar',
  data () {
    return {
      editor: null,
      info_: null
    }
  },
  model: {
    prop: 'value',
    event: 'change'
  },
  props: {
    value: {
      type: String,
      required: true
    },
    isClear: {
      type: Boolean,
      default: false
    }
  },
  watch: {
    isClear (val) {
      if (val) {
        this.editor.txt.clear()
        this.info_ = null
      }
    },
    value (val) {
      //  v-model 
      this.editor.txt.html(val)
    }
  },
  mounted () {
    this.seteditor()
    this.editor.txt.html(this.value)
  },
  methods: {
    seteditor () {
      this.editor = new E(this.$refs.toolbar, this.$refs.editor)

      this.editor.customConfig.uploadImgShowBase64 = true // base 64 
      this.editor.customConfig.uploadImgMaxSize = 3 * 1024 * 1024 // 3M

      // 
      this.editor.customConfig.menus = [
        'fontSize', // 
        'bold', // 
        'fontName', // 
        'italic', // 
        'underline', // 
        'strikeThrough', // 
        'foreColor', // 
        'backColor', // 
        'link', // 
        'justify', // 
        'image', // 
        'table',
      ]

      this.editor.customConfig.uploadImgHooks = {
        fail: (xhr, editor, result) => {
          // 
        },
        success: (xhr, editor, result) => {
          // 
        },
        timeout: (xhr, editor) => {
          // 
        },
        error: (xhr, editor) => {
          // 
        },
        customInsert: (insertImg, result, editor) => {
          // ,
        }
      }
      this.editor.customConfig.onchange = (html) => {
        this.info_ = html // 
        this.$emit('change', this.info_) // 
      }

      // 
      this.editor.create()
    }
  }
}
</script>

<style lang="css">
.editor {
  width: 80%;
  margin: 0 auto;
}
.toolbar {
  border: 1px solid #ccc;
}
.text {
  border: 1px solid #ccc;
  height: 500px;
}
</style>


